import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false

App.mpType = 'app'

import request from '@/common/request.js'
Vue.prototype.$req = request;

import globalDataAudio from '@/common/globalDataAudio.js';

Vue.prototype.$globalDataAudio = globalDataAudio;

// 引入全局uView
import uView from 'uview-ui'
Vue.use(uView);

Vue.mixin({
	data(){
		return {
		}
	},
	methods: {
		toUrl(url) {
			if(url.includes('tabbar')){
				uni.switchTab({
					url
				})
				return
			}
			uni.navigateTo({
				url
			})
		},
		toBack(){
			if(getCurrentPages().length<=1){
				uni.switchTab({
					url: "/pages/tabbar/index/index"
				})
				return
			}
			uni.navigateBack({
				delta: 1
			})
		}
	}
})

const app = new Vue({
    ...App
})
app.$mount()